

import { NbMenuService, NbSidebarService, NbSearchService } from '@nebular/theme';
import { UserService } from '../../../@core/data/users.service';
import { AnalyticsService } from '../../../@core/utils/analytics.service';
import { RouterModule, Router, ActivatedRoute } from '@angular/router';

import {TranslateService} from '@ngx-translate/core';

import { Component, Input, OnInit,
  Pipe } from '@angular/core';



@Component({
  selector: 'ngx-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
})
export class HeaderComponent implements OnInit   {

  @Input() value: string | number;
  @Input() position = 'normal';
 
  user: any;

  userMenu = [ { title: 'Log out' , link: '/'}];

  constructor(
    private _route: ActivatedRoute,
    private translate:TranslateService,
    private sidebarService: NbSidebarService,
              private menuService: NbMenuService,
              private userService: UserService,
              private analyticsService: AnalyticsService,
            private router:Router,
            private searchService: NbSearchService,
         ) {
              
            translate.addLangs(["en", "fr"]);
            translate.setDefaultLang('en');
            let browserLang = translate.getBrowserLang();
            translate.use(browserLang.match(/en|fr/) ? browserLang : 'en');
  }

  onLanguageChange(ln)
  {
    this.translate.use(ln);
    localStorage.setItem('lang', ln);
    //window.location.reload();

    this.onRefresh();
    // var url =this._route.snapshot.url;
    // this.router.navigate(["pages/editors/control-new/fr"]);
  }
  onRefresh() {
    this.router.routeReuseStrategy.shouldReuseRoute = function(){return false;};
  
    let currentUrl = this.router.url + '?';
  
    this.router.navigateByUrl(currentUrl)
      .then(() => {
        this.router.navigated = false;
        this.router.navigate([this.router.url]);
      });
    }

  ngOnInit() {
    this.translate.use(localStorage.getItem('lang'));

    this.userService.getUsers()
      .subscribe((users: any) =>
      {
         this.user = users.admin});
      // this.searchService.
      
      this.searchService.onSearchSubmit().subscribe((data: { term: string, tag: string }) => {
       
        // this.router.navigate(["pages/tables/details/"+data.term+'?']);
        

        localStorage.setItem('transactionreference', data.term);
        this.router.navigate(["pages/tables/details"]);
        if(this.router.url.includes("pages/tables/details"))
          this.onRefresh();
       
      });


  }

  getInitials(name): string {
    if (name) {
      const names = name.split(' ');

      return names.map(n => n.charAt(0)).splice(0, 2).join('').toUpperCase();
    }

    return '';
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }

  toggleSettings(): boolean {
    this.sidebarService.toggle(false, 'settings-sidebar');
    return false;
  }

  goToHome() {
    this.menuService.navigateHome();
  }

  startSearch() {
    //this.router.navigate(["pages/tables/details/"]);
  }


}



