import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { catchError, map, tap } from 'rxjs/operators';
import { environment } from '../../../../environments/environment';
import { DashboardTable } from '../entities/dashboard';






@Injectable()
export class DashboardService {

 region:string;
  constructor(
    private http: HttpClient ){

     
    }

  private data = [];
    

/** GET hero by id. Will 404 if id not found */
  getMonthlyTrends(): Observable<any> {   
    this.region=localStorage.getItem('region');
    const url = `${environment.baseUrl}MonthlyTrends/${this.region}`;
    return this.http.get<any>(url).pipe(
      tap(_ => this.log(`fetched Monthly Trends`)),
      catchError(this.handleError<any>(`Monthly Trends Error`))
    );
  }


  fetchEvidenceCount():Observable<any>{
    //return this.http.get<any>(`${environment.baseUrl}EvidenceCount`)
    return this.http.get<any>(`${environment.baseUrl}EvidenceCount`).pipe(
      tap(_ => this.log(`fetched Evidence Count`)),
      catchError(this.handleError<any>(`Evidence Count Error`))
    );
  }

  fetchDashboardTableData():Observable<Array<DashboardTable>>{
    //return this.http.get<any>(`${environment.baseUrl}EvidenceCount`)
    return this.http.get<Array<DashboardTable>>(`${environment.baseUrl}GetDashboardTableData`).pipe(
      tap(_ => this.log(`fetched dashboard table`)),
      catchError(this.handleError<any>(`dashboard table Error`))
    );
  }

  fetchDashboardLiveData():Observable<any>{
    //return this.http.get<any>(`${environment.baseUrl}EvidenceCount`)
    return this.http.get<any>(`${environment.baseUrl}GetDashboardLiveData`).pipe(
      tap(_ => this.log(`fetched dashboard live`)),
      catchError(this.handleError<any>(`dashboard live Error`))
    );
  }

  
/** GET hero by id. Will 404 if id not found */
getErrorByCategory(): Observable<any> {
  this.region=localStorage.getItem('region');
  const url = `${environment.baseUrl}ErrorByCategory/${this.region}`;
  return this.http.get<any>(url).pipe(
    tap(_ => this.log(`fetched ErrorByCategory`)),
    catchError(this.handleError<any>(`Monthly ErrorByCategory`))
  );
}

fetchEvidenceMonthWise():Observable<any>{
  //return this.http.get<any>(`${environment.baseUrl}EvidenceCount`)
  return this.http.get<any>(`${environment.baseUrl}EvidenceByMonth`).pipe(
    tap(_ => this.log(`fetched Evidence Month wise`)),
    catchError(this.handleError<any>(`Evidence Month wise Error`))
  );
}

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  /** Log a HeroService message with the MessageService */
  private log(message: string) {
    console.log(message);
  }


}



